import Link from 'next/link'

const linkStyle = {
  float: 'left',
  width: '260px'
}

const Sidebar = () => (
    <div>
        <Link href="/">
          <a style={linkStyle}>Home</a>
        </Link>
        <Link href="/about">
          <a style={linkStyle}>About</a>
        </Link>
    </div>
)

export default Sidebar



