import Header from './Header'
import Sidebar from './Sidebar'

const layoutStyle = {
  margin: '0px auto',
  padding: 20,
  width: '960px',
  textAlign: 'left',
  border: '1px solid #DDD'
}

const Layout = (props) => (
  <div style={layoutStyle}>
    <Header />
    <Sidebar />
    {props.children}
  </div>
)

export default Layout



